/* for LINK SECTION */

  .container1 {
    position: relative;
    width:100%;
    height: 100vh;
    overflow: hidden;
  }
  .link{
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
  }
  .link-img{
    width: 100%;
    height: 100%;
  }
  .link-img1{
    height: 50%;
    width: 100%;
    align-items: center;

  }
  
  .first-div {
    width:50%;
    height: 33%;
    z-index: 1;
    transition: left 1.6s;
  }
  
  .second-div {
    width:25%;
    height:33%;
    transition: left 1.6s;
  }
  .third-div {
    width:25%;
    height:33%;
    transition: left 1.6s;
  }
  .fourth-div {
    width:25%;
    height:33%;
    transition: top 1.6s;
  }
  .fifth-div {
    width:25%;
    height:33%;
    transition: top 1.6s;
    transition: left 1.6s;
  }
  .sixth-div {
    width:50%;
    height:33%;
    transition: top 1.6s;
    transition: left 1.6s;
  }
  .seventh-div {
    width:25%;
    height:33%;
    transition: top 1.6s;
    transition: left 1.6s;
  }
  .eighth-div {
    width:50%;
    height:33%;
    transition: top 1.6s;
    transition: left 1.6s;
  }
  .ninth-div {
    width:25%;
    height:33%;
    transition: top 1.6s;
    transition: left 1.6s;
  }



  .container1:hover .first-div {
    left: 0;
    z-index: 1;
    transition: left 1.6s;
  }
  
  .container1:hover .second-div {
    left: 50%;
    width: 25%;
    transition: left 1.6s;
  }

  .container1:hover .third-div {
    left: 75%;
    width: 25%;
    transition: left 1.6s;
  }
  .container1:hover .fourth-div {
    left: 0;
    top: 33%;
    width: 25%;
    transition: top 1.6s;
  }
  .container1:hover .fifth-div {
    left: 25%;
    top: 33%;
    width: 25%;
    transition: top 1.6s;
    
  }
  .container1:hover .sixth-div {
    left: 50%;
    top: 33%;
    width: 50%;
    transition: left 1.6s;
  }
  .container1:hover .seventh-div {
    left: 0;
    top: 66%;
    width: 25%;
    transition: top 1.6s;
  }
  .container1:hover .eighth-div {
    left: 25%;
    top: 66%;
    width: 50%;
    transition: top 1.6s; 
    transition: left 1.6s;
  }
  .container1:hover .ninth-div {
    left: 75%;
    top: 66%;
    width: 25%;
    transition: top 1.6s;
    transition: left 1.6s;
  }


